/**
 * Classe com mtodos que so reaproveitados em no projeto
 */
export class Utils {
  /**
  * retorna o ms por extenso.
  * @param date
  * @argument number 1-12
  * @returns {boolean}
  */
  public static mesPorExtenso(month: string): string {
    let m;
    switch (Number(month.replace(/^0*/, ''))) {
      case 1: {
        m = 'janeiro';
        break;
      }
      case 2: {
        m = 'fevereiro';
        break;
      }
      case 3: {
        m = 'maro';
        break;
      }
      case 4: {
        m = 'abril';
        break;
      }
      case 5: {
        m = 'maio';
        break;
      }
      case 6: {
        m = 'junho';
        break;
      }
      case 7: {
        m = 'julho';
        break;
      }
      case 8: {
        m = 'agosto';
        break;
      }
      case 9: {
        m = 'setembro';
        break;
      }
      case 10: {
        m = 'outubro';
        break;
      }
      case 11: {
        m = 'novembro';
        break;
      }
      case 12: {
        m = 'dezembro';
        break;
      }
      default: {
        m = 'No  um ms vlido!';
        break;
      }
    }

    return m;
  }
  /**
   * retorna true se o cnpj informado for vlido.
   * @param cnpj
   * @returns {boolean}
   */
  public static validaCNPJ(cnpj: string): boolean {
    console.log(cnpj);
    // Primeiro dgito:
    // Uma sequncia que   mascara de pesos publicada para validao.
    const sequence = [5, 4, 3, 2, 9, 8, 7, 6, 5, 4, 3, 2];
    const sumArray = new Array(sequence.length);
    const cnpjArray = cnpj.split('');
    // console.log(cnpjArray);

    // Multiplica cada dgito do cnpj pelo peso que se encontra na mesma posio.
    // Ex:13.347.016/0001-VV Sequencia de Pesos: [5, 4, 3, 2, 9, 8, 7, 6, 5, 4, 3, 2];
    // A multiplicao seria 1*5, 3*4,  3*3 ...
    for (let i = 0; i < sequence.length; i++) {
      const value = Number(cnpjArray[i]);
      sumArray[i] = sequence[i] * value;
    }
    // Soma todas as posies
    let totalSumArray = Utils.somaTodasPosicoesdeUmArray(sumArray);

    // Obtem o resto da diviso por 11 e subtrae base 11 do restante da diviso.
    let result = 11 - (totalSumArray / 11);

    // Adiciona o digito encontrado ao CNPJ.
    cnpjArray.push(result.toString());


    // Segundo dgito:
    const sequenceSecondDigit = [6, 5, 4, 3, 2, 9, 8, 7, 6, 5, 4, 3, 2];
    const sumArray2 = new Array(sequenceSecondDigit.length);

    for (let i = 0; i < sequenceSecondDigit.length; i++) {
      const value = Number(cnpjArray[i]);
      sumArray2[i] = sequenceSecondDigit[i] * value;
    }

    totalSumArray = Utils.somaTodasPosicoesdeUmArray(sumArray2);

    result = 11 - (totalSumArray / 11);

    cnpjArray.push(result.toString());

    const valid = cnpj === cnpjArray.toString() ? true : false;
    console.log(valid);
    return valid;
  }

  /**
  * retorna resultado da soma de todas as posies de um array.
  * @param sumArray
  * @returns {number}
  */
  public static somaTodasPosicoesdeUmArray(sumArray: number[]): number {
    let result = 0;
    sumArray.forEach(element => {
      result += element;
    });
    return result;
  }

  /**
  * retorna resultado da soma de todas as posies de um array.
  * @param string
  * @returns {string}
  */
  public static formatPhoneNumber(v: string): string {
    v = v.replace(/\D/g, ''); // Remove tudo o que no  dgito
    v = v.substring(0, 11); // No permite que ultrapasse o nmero mximo de caracteres
    v = v.replace(/^(\d{2})(\d)/g, '($1) $2'); // Coloca parnteses em volta dos dois primeiros dgitos
    v = v.replace(/(\d)(\d{4})$/, '$1-$2'); // Coloca hfen entre o quarto e o quinto dgitos
    return v;
  }

  public static extractValue(row, cell): string {
    let result = row;
    const properties = cell.split('.');

    properties.forEach(property => {
      result = result[property] ? result[property] : result;
    });
    return result;
  }
}
